import React, { lazy, Suspense, useContext } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

// import Projects from './Pages/Projects'
import Layout from "./pages/Layout";
import ContentLoader from "./contexts/ContentLoader";

const Home = lazy(() => import("./pages/Home"));
const AboutMe = lazy(() => import("./pages/AboutMe"));
const Training = lazy(() => import("./pages/Training"));
const Techs = lazy(() => import("./pages/Techs"));
const Error404 = lazy(() => import("./pages/Error404"));
const TechDetail = lazy(() => import("./pages/Techs/TechDetail"));
const CertsPlatzi = lazy(() => import("./pages/Training/CertsPlatzi"));
const CertDetail = lazy(() => import("./pages/Training/CertDetail"));

const App = () => {
  const { loader } = useContext(ContentLoader);

  return (
    <>
      <Router>
        <Layout>
          <Suspense fallback={loader}>
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/aboutme" exact component={AboutMe} />
              {/* <Route path="/projects" exact component={Projects} /> */}
              <Route path="/technologies" exact component={Techs} />
              <Route path="/technologies/:tech" exact component={TechDetail} />
              <Route path="/training" exact component={Training} />
              <Route path="/training/platzi" exact component={CertsPlatzi} />
              <Route path="/training/:cert" exact component={CertDetail} />
              <Route path="*" component={Error404} />
            </Switch>
          </Suspense>
        </Layout>
      </Router>
    </>
  );
};

export default App;
