import { useState, useEffect } from "react";
import Spinner from "../components/Spinner";
import HomeLoader from "../pages/Home/Loader";

export default function useLoader(location) {
  const [loader, setLoader] = useState("Cargando...");
  useEffect(() => {
    switch (location) {
      case "/":
        setLoader(<HomeLoader className="home-loader" />);
        break;
      default:
        setLoader(
          <Spinner
            type="Puff"
            color="white"
            style={{ marginTop: "2em", textAlign: "center" }}
          />
        );
        break;
    }
  }, [location]);

  return { loader, setLoader };
}
