import { createContext, useContext } from "react";
import useLoader from "../hooks/useLoader";
import LocationContext from "./Location";

const ContentLoaderContext = createContext({});

export const ContentLoaderProvider = ({ children }) => {
  const { location } = useContext(LocationContext);
  const { loader, setLoader } = useLoader(location);

  return (
    <ContentLoaderContext.Provider value={{ loader, setLoader }}>
      {children}
    </ContentLoaderContext.Provider>
  );
};

export default ContentLoaderContext;
