// Header Component

import React, { lazy, Suspense, useContext } from 'react'
import './header.scss'

import MenuIcon from '../../../components/icons/MenuIcon'
import SocialNav from '../SocialNav'
import HeaderLoader from './Loader'
import ToggleMenuContext from '../../../contexts/ToggleMenu'

const HeaderInfo = lazy(() => import('../HeaderInfo'))

const Header = () => {
  const { toggleMenu } = useContext(ToggleMenuContext)

  return (
    <header className="header">
      <div className="header-container">
        <div className="header-content">
          <Suspense fallback={<HeaderLoader className="header-loader" />}>
            <HeaderInfo />
          </Suspense>
          <div className="header-iconMenu">
            <MenuIcon onClick={(e) => toggleMenu(e)}/>
          </div>
          <div className="header-socialnav">
            <SocialNav />
          </div>
        </div>
      </div>
    </header>
  )
}

export default React.memo(Header)
